import React from 'react';
import { Route,Redirect} from "react-router-dom";
import { connect } from "react-redux";

//<Admin 
//higher order component: l 1 function hoc component nhn vo 1 tham s l 1 component khc
function AdminRoute(props) {
    const { component: Component, currentUser, ...routerProps } = props;
    //currentUser: l children
    //routerProps => {exact, path}
    // render() {
    return <Route {...routerProps} render={(props) => {
        if (currentUser) {
            // ng nhp
            if (currentUser.maLoaiNguoiDung === "GV") {
                return <Component {...props}/> //Ly cc props history, location, match
            }
            // ng nhp nhng khng l ging vin
            return <Redirect to="/coursesDetail" />
        }
        //Cha ng nhp
        return <Redirect to="/login" />
        // return <Component {...props}/>
    }} />
    // }
}
const mapStateToProps = (state) => {
    return {
        currentUser: state.loginReducer.currentUser,
    };
};

export default connect(mapStateToProps)(AdminRoute)
