import React, { useState } from 'react';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import KeyboardVoiceIcon from '@material-ui/icons/KeyboardVoice';

export default function Carousel() {
    const [color, setColor] = useState('#f00')
    const handleChange = (evt) => {
        setColor(evt.target.value)
    }
    const useStyle = makeStyles((theme) => ({
        label: {
            color: color,
        },
        button: {
            margin: theme.spacing(1),
        },
        text1:{
            color: 'white'
        },
        text2:{
            color: 'black'
        }
    }));


    const classes = useStyle();


    // render() {
    return (
        <div id="carouselExampleIndicators" className="carousel slide carousel-fade" data-ride="carousel">
            <ol className="carousel-indicators">
                <li data-target="#carouselExampleIndicators" data-slide-to={0} className="active" />
                <li data-target="#carouselExampleIndicators" data-slide-to={1} />
                <li data-target="#carouselExampleIndicators" data-slide-to={2} />
            </ol>
            <div className="carousel-inner">

                <div className="carousel-item active">
                    <img className="d-block w-100" src="img/slide1.jpg" alt="First slide" />
                    <div className="carousel-info">
                        <h2 className={classes.text1}>Hy khm ph cc kha hc</h2>
                        <p className={classes.text1}>Hy trang b kin thc  sn sng trong tng lai bng cc kha hc cht lng. Bn s t c c m ngh nghip ca mnh.</p>
                        <p className={classes.text1}>Hy th ngay hm nay.</p>
                        <Button variant="outlined" color="primary" className={classes.label}>Tham gia</Button>
                        <Button
                            variant="contained"

                            color="secondary"
                            className={classes.button}
                            startIcon={<KeyboardVoiceIcon />}
                        >
                            Tr chuyn
                        </Button>
                    </div>
                </div>
                <div className="carousel-item">
                    <img className="d-block w-100" src="img/cover.jpg" alt="Second slide" />
                    <div className="carousel-info">
                        <h2 className={classes.text1}>Hc, hc na, hc mi</h2>
                        <p className={classes.text1}>ng bao gi b l c hi c hc ca bn vi nhng kha hc cht lng, i ng ging vin chuyn nghip. Kin thc s l th gip chng ta tn ti</p>
                        <p className={classes.text1}>ng chn ch na.</p>
                        <Button variant="contained" color="primary">Tham gia</Button>
                        <Button
                            variant="contained"

                            color="secondary"
                            className={classes.button}
                            startIcon={<KeyboardVoiceIcon />}
                        >
                            Tr chuyn
      </Button>
                    </div>
                </div>
                <div className="carousel-item">
                    <img className="d-block w-100" src="img/cathy-bg2.jpg" alt="Third slide" />
                    <div className="carousel-info">
                        <h2>Bn yu thch lp trnh ?</h2>
                        <p>Ti Steven, chng ti c v vn cc kha hc  bn la chn t lp trnh, dy nu n cho n qun l doanh nghip. Hy xy dng mt nn tng vng chc cho bn thn.</p>
                        <p>Hy th ngay hm nay.</p>
                        <Button variant="outlined" color="primary" className={classes.label}>Tham gia</Button>
                        <Button
                            variant="contained"

                            color="secondary"
                            className={classes.button}
                            startIcon={<KeyboardVoiceIcon />}
                        >
                            Tr chuyn
      </Button>

                    </div>
                </div>

            </div>
            <a className="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
                <span className="carousel-control-prev-icon" aria-hidden="true" />
                <span className="sr-only">Previous</span>
            </a>
            <a className="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
                <span className="carousel-control-next-icon" aria-hidden="true" />
                <span className="sr-only">Next</span>
            </a>
        </div>
    )
    // }
}