import React, { useState } from 'react';
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import IconButton from '@material-ui/core/IconButton';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';

export default function Numbers() {
    const [move, setMove] = useState(0)
    const [windowWidth, setWindowWidth] = useState(window.outerWidth)
    const feedBackContent = [
        {
            id: 1,
            img: '/img/ava1.jpg',
            name: "Trn Minh",
            content: "Ti khng c thi gian v tin bc  hc i hc. Mc tiu ca ti l tr thnh mt ngi lp trnh web t do, v nh c Steven, ti thc s  t c mc tiu.",
        },
        {
            id: 2,
            img: '/img/ava2.jpg',
            name: "Huyn C",
            content: "Hc, hc na, hc mi v Steven l mt ni tuyt vi  gip mnh thc hin c chm ngn . Mnh  hc c rt nhiu v gii thiu n cho tt c bn b ca mnh",
        },
        {
            id: 3,
            img: '/img/ava3.jpg',
            name: "Cm Tin",
            content: "Cc con ca ti v ti YU Steven! Cc kha hc tht tuyt vi v cc ging vin rt vui v v trnh  cao. Ti ch c chng ti tm thy n sm hn.",
        },
        {
            id: 4,
            img: '/img/ava1.jpg',
            name: "Trn Bnh",
            content: "Rt hay v b ch",
        },
        {
            id: 5,
            img: '/img/ava2.jpg',
            name: "Minh Phng",
            content: "ng bao gi thay i mnh v ngi khc. Nu h khng th tip nhn mt con ngi nhiu im xu l bn, th cng khng xng  c c mt con ngi vi nhiu im tt l bn.",
        },
        {
            id: 6,
            img: '/img/ava1.jpg',
            name: "Thu H",
            content: "Bn cn sc mnh, ngh lc nn cuc sng  t ra nhng kh khn nghch cnh  bn vt qua v tr nn mnh m hn",
        },
        {
            id: 7,
            img: '/img/ava2.jpg',
            name: "B T",
            content: "Hy cm n nhng lc bn gp kh khn, bi nu khng c kh khn, bn s khng c c hi  hiu mnh v tri nghim cuc sng.",
        }
    ]
    let itemDom = document.getElementsByClassName("item")
    Array.from(itemDom).forEach(item => {
        item.style.transform = `translateX(${100 * move}%)`
    })
    window.addEventListener("resize", (e)=>{
        setWindowWidth(e.target.outerWidth)
    });
    // Responsive
    let numberOfItems = 3;
    if(windowWidth > 992){
        numberOfItems = 3;
    }else if(windowWidth <= 992 && windowWidth > 576){
        numberOfItems = 2;
    }else if(windowWidth < 576){
        numberOfItems = 1;
    }
    // Responsive
    const handleCarousel = (value) => {
        if (value === "right") {
            if (move > -(feedBackContent.length - numberOfItems)) {
                setMove(move - 1)
            }
        }
        if (value === "left") {
            if(move < 0){
                setMove(move + 1)
            }
        }
    }
    return (
        <section id="students" className="testimonial">
            <div className="feedback">
                <Typography className="feedback__title" variant="h2" align="center">Cm ngh ca hc vin</Typography>
                <div className="feedback_content">
                    {feedBackContent.map((item, index) => {
                        return (
                            <React.Fragment key={index} >
                                <div id={`item${item.id}`} className="item">
                                    <div class="miniItem">
                                        <img src={item.img} alt />
                                        <b>{item.id}. {item.name}</b>
                                        <p>{item.content}</p>
                                    </div>
                                    
                                </div>
                            </React.Fragment>
                        )
                    })}
                </div>
                {move > -(feedBackContent.length-numberOfItems)?<IconButton id="button2" className="button" variant="contained" color="primary" onClick={() => handleCarousel("right")}>
                    <ChevronRightIcon fontSize="inherit" />
                </IconButton>:null}
                {move < 0? <IconButton id="button1" className="button" variant="contained" color="primary" onClick={() => handleCarousel("left")}>
                    <ChevronLeftIcon fontSize="inherit" />
                </IconButton>:null}          
            </div>
            <div className="testimonial_companies">
                <Container maxWidth="lg">
                    <Box p={8}>
                    <Typography className="titleNumber" variant="h5" align="center">HP TC VI NHNG CNG TY HNG U</Typography>
                    </Box>
                    <Grid container justify="space-around" className="companies_logo" spacing={3} >
                        <Grid item>
                            <img src="./img/booking.svg" alt />
                        </Grid>
                        <Grid item>
                            <img src="./img/mercedes.svg" alt />
                        </Grid>
                        <Grid item>
                            <img src="./img/volkswagen.svg" alt />
                        </Grid>
                        <Grid item>
                            <img src="./img/pinterest.svg" alt />
                        </Grid>
                        <Grid item>
                            <img src="./img/adidas.svg" alt />
                        </Grid>
                        <Grid item>
                            <img src="./img/paypal.svg" alt />
                        </Grid>
                    </Grid>
                    <Grid container className="moreInfo" spacing={5}>
                        <Grid item xs={12} md={6} className="moreInfo__item">
                            <h4>Tr thnh ging vin</h4>
                            <p>Cc ging vin hng u t khp ni trn th gii dy hng triu hc vin trn Steven. Chng ti cung cp cc cng c  bn dy nhng g bn yu thch.</p>
                            <Button className="redButton" variant="outlined" color="secondary">Cng tc ngay hm nay</Button>
                        </Grid>
                        <Grid item xs={12} md={6} className="moreInfo__item">
                            <h4>Steven cho Doanh nghip</h4>
                            <p>Nhn quyn truy cp khng gii hn vo hn 4.000 kha hc hng u ca Steven cho nhm ca bn.</p>
                            <Button className="redButton" variant="outlined" color="secondary">ng k Steven cho Doanh nghip</Button>
                        </Grid>
                    </Grid>
                </Container>
            </div>
        </section>

    )
}