import React,{useState} from 'react'
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Hidden from '@material-ui/core/Hidden';

const useStyles = makeStyles((theme) => ({
    formControl: {
      margin: theme.spacing(1),
      minWidth: 130,
    },
    background:{
        backgroundColor:'#fff',
        color:'white'
    }
   
  }));

export default function Footer() {
    const classes = useStyles();
    const [language,setLanguage] = useState("vietnam")
    console.log(language)
    return (
        <footer className={classes.background}>
            <Grid container className="border-bottom pb-3">
                <Grid container item xs={12} sm={9} className="footer__content" justify="space-around">
                    <Grid item xs={12} sm={4} className="footer__item">
                        <a href="#" className="link-active">Steven cho Doanh nghip</a>
                        <a href="#" className="link-active">Dy trn Steven</a>
                        <a href="#">ng dng Steven</a>
                        <a href="#">V chng ti</a>
                    </Grid>
                    <Grid item xs={12} sm={4} className="footer__item">
                        <a href="#">Lin h</a>
                        <a href="#">Tuyn dng</a>
                        <a href="#">Blog</a>
                        <a href="#">H tr</a>
                    </Grid>
                    <Grid item xs={12} sm={4} className="footer__item">
                        <a href="#">Lin kt</a>
                        <a href="#">Sitemap</a>
                        <a href="#">Cc kha hc</a>
                    </Grid>

                </Grid>
                <Grid item xs={12} sm={3}>
                    <div className="btn-group dropup">
                        <FormControl variant="filled" className={classes.formControl}>
                            <InputLabel id="select-languages-title">
                                <i className="fa fa-globe-americas" /> Languages
                            </InputLabel>
                            <Select
                                labelId="select-languages-title"
                                id="select-languages"
                                value={language}
                                onChange={(evt)=> {setLanguage(evt.target.value)}}
                                label="Language"
                            >
                                <MenuItem value={"english"}>English</MenuItem>
                                <MenuItem value={"vietnam"}>Ting Vit</MenuItem>
                                <MenuItem value={"chinese"}>Trung Quc</MenuItem>
                                <MenuItem value={"thailand"}>Thi Lan</MenuItem>
                                <MenuItem value={"deutsch"}>H Lan</MenuItem>
                            </Select>
                        </FormControl>
                    </div>
                </Grid>
            </Grid>
            
            <Grid container justify="space-between" className="copyright">
                <Grid item>
                    <img src="/img/logo2.png" width="60px"/>
                    <small style={{display:'inline-block',padding:'10px'}}> Copyright  2021 Steven, Inc.</small>
                </Grid>
                <Hidden xsDown>
                    <Grid item>
                        <a href="#">iu khon</a>
                        <a href="#">Chnh sch v quyn ring t</a>
                    </Grid>
                </Hidden>
            </Grid>
        </footer>

    )
}
